<template>
	<div>
		<identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
		<face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
		<contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
		<process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'process')" :handle-business="handleBusiness"/>
		<input-car
				:carTypeOptions="businessTypeCode === BUSINESS_EN_NAME_CODE.walkLicenseExchange ? [{ label: '小型汽车', value: '02', key: 2 }] : CAR_TYPE_OPTIONS"
				v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')"
				:nextFunction="inputCarInfo"
		/>
		<text-appear v-if="currentStep === steps.findIndex(step=>step.key === 'carInfo')" title="机动车信息确认" :formListData="carInfo" :media="mediaCarInfo"></text-appear>
		<reason-choose v-if="currentStep === steps.findIndex(step=>step.key === 'reasonUpload')" :nextFunction="reasonUpload" :reasons="reasons"/>
		<mail-type v-if="currentStep === steps.findIndex(step=>step.key === 'mailType')" :nextFunction="mailAddressUpload" :placeOfCollectionArr="placeOfCollectionArr" :mailTip="mailTip"/>
		<process :type="2" v-if="currentStep === steps.findIndex(step=>step.key === 'getLsh')" :handle-business="getLsh"/>

		<unified-payment-choose v-if="currentStep === steps.findIndex(step=>step.key === 'payChoose')" :payQrCode="payQrCode" :nextFunction="getUnifiedPayment"/>
		<unified-payment-appear v-if="currentStep === steps.findIndex(step=>step.key === 'payResult')" :payQrCode="payQrCode" :nextFunction="checkUnifiedPayment"/>

		<process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'processPrint')" :handle-business="vehLicenseChangeGetPaperPath"/>
		<process :type="4" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handle-business="tickPrint" waitText="业务凭条打印中，请稍后......"/>
		<process :type="5" v-if="currentStep === steps.findIndex(step=>step.key === 'processComplete')" :handle-business="getResult"/>
		<complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="completeInfo" footTexts="温馨提示：请取走业务申请表或凭条"/>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import complete from '@/components/complete';
import textAppear from '@/view/pageComponents/text-appear';
import mailType from '@/view/pageComponents/mail-type';
import reasonChoose from '@/view/pageComponents/reason-choose';
import unifiedPaymentChoose from '@/view/pageComponents/unified-payment-choose';
import unifiedPaymentAppear from '@/view/pageComponents/unified-payment-appear';
import inputCar from '@/view/pageComponents/input-car';
import process from '@/components/process';
import { VEHICLE_LICENSE_RENEWAL } from '@/constant/stepsOption';
import { vehLicenseChangeCarInfo, vehLicenseChangeComplete, vehLicenseChangeFace, vehLicenseChangeGetPaperPath, vehLicenseChangeGetPayStatus, vehLicenseChangeSavePhoto, vehLicenseCheckPinCode, vehLicensePinCode, vehLicenseSaveMailInfo } from '@/service/traffic/vehicle/licenseRenewal';
import { cancelPrefix } from '@/utils/tools';
import mediaCarInfo from '@/assets/media/trafic/15.mp3';
// import { tickPrint } from '@/service/HardwareRequest';
// import { getCarTick } from '@/constant/tickertape';
import { excelPrint } from '@/service/common';
import request from '@/utils/request';
import { CAR_TYPE_OPTIONS } from '@/constant/constant';
// import { checktUnifiedPayment, getUnifiedPayment } from '@/service/traffic/traffic';

export default {
    name: 'license-renewal-index',
    components: { identifyCardPage, faceYitu, contrast, complete, inputCar, textAppear, mailType, reasonChoose, unifiedPaymentChoose, unifiedPaymentAppear, process },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            businessTypeName: 'businessTypeName',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        }),
        ...mapState('permanent', {
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE',
            originBusinessObject: 'originBusinessObject'
        })
    },
    data () {
        return {
            placeOfCollectionArr: [
                { type: '30', value: '邮寄' }
            ],
            reasons: [{ label: '丢失', value: 'B' }, { label: '损坏', value: 'C' }, { label: '灭失', value: 'A' }],
            mailTip: '提示：选择邮寄方式，证件将在两个工作日内寄出，邮寄费用以到付方式收取，标准参考如下，以实际付费为准: 蚌埠一市三县20元（车牌25元），省内25元（车牌30元），跨省30元（车牌30元）',
            reason: '',
            mediaCarInfo: mediaCarInfo,
            hphm: '',
            hpzl: '',
            completeInfo: {},
            carInfo: {},
            lsh: '',
            paperPath: '',
            payQrCode: '',
            payQr: '',
            CAR_TYPE_OPTIONS: CAR_TYPE_OPTIONS
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage', 'setUserRecognitionId', 'setUserBusinessId', 'setCurrentBusinessHandle', 'setCurrentBusinessName']),
        async handleBusiness () {
            const res = await vehLicenseChangeFace({
                idNum: this.identifyCard.certNumber,
                name: this.identifyCard.partyName,
                idNumPhoto: cancelPrefix(this.identifyCard.identityPic),
                identifiedPhoto: cancelPrefix(this.binocularCamera)
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async inputCarInfo ({ carLicense, carType }) {
            this.hphm = carLicense;
            this.hpzl = carType;
            const res = await vehLicenseChangeCarInfo({
                name: this.identifyCard.partyName,
                hphm: carLicense,
                hpzl: carType
            });
            if (res && res.code === '0') {
                this.carInfo = res.result;

                if (res.result.status === 1) { // slh获得，pincdoe未成功
                    this.handleRepeatBusiness(res);
                    this.changeStep('payChoose'); // todo 支付未接入
                } else if (res.result.status === 2) { // pincode成功，支付未成功
                    this.payQrCode = res.result.pinCode;
                    this.handleRepeatBusiness(res);
                    this.changeStep('payResult'); // todo 支付未接入
                } else if (res.result.status === 3) { // 支付成功，未办结
                    this.payQrCode = res.result.pinCode;
                    this.handleRepeatBusiness(res);
                    this.changeStep('processPrint');
                } else {
                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        handleRepeatBusiness (res) {
            this.lsh = res.result.lsh;
            this.setUserRecognitionId(res.result.userRecognitionId);
            this.setUserBusinessId(res.result.businessId);
            this.setCurrentBusinessHandle(res.result.businessTypeCode);
            this.setCurrentBusinessName(this.originBusinessObject.find((i) => {
                return i.businessCode === res.result.businessTypeCode;
            }).businessName);
        },
        async mailAddressUpload (mailInfo) {
            const res = await vehLicenseSaveMailInfo({
                idNum: this.identifyCard.certNumber,
                ...mailInfo
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        reasonUpload (reason) {
            this.reason = reason;
            this.changeStep('down');
        },
        async checkPayStatus () {
            const res = await vehLicenseChangeGetPayStatus();
            if (res && res.code === '0') {
                this.changeStep('down');
            }
        },
        async vehLicenseChangeGetPaperPath () {
            const res = await vehLicenseChangeGetPaperPath();
            if (res && res.code === '0') {
                this.paperPath = res.result.paperPath;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getResult () {
            const res = await vehLicenseChangeComplete();
            if (res && res.code === '0') {
                this.completeInfo = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getLsh () {
            const res = await vehLicenseChangeSavePhoto({
                sqyy: this.reason
            });
            if (res && res.code === '0') {
                this.lsh = res.result.lsh;
                // this.payQr = res.hlwptsfewm;
                // this.jbr = res.jbr;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async getUnifiedPayment () {
            const res = await vehLicensePinCode({ lsh: this.lsh });
            if (res && res.code === '0') {
                this.payQrCode = res.result.pinCode;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async checkUnifiedPayment () {
            const res = await vehLicenseCheckPinCode();
            if (res && res.code === '0') {
                this.changeStep('down');
            }
        },
        async tickPrint () {
            const res = await excelPrint({
                path: request.getServerUrl() + this.paperPath
            });
            if (res && res.code === 1) {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }
            // const data = {
            //     businessTypeName: this.businessTypeName,
            //     hphm: this.hphm,
            //     hpzl: this.hpzl,
            //     idNum: this.identifyCard.certNumber,
            //     name: this.identifyCard.partyName,
            //     clsbdh: this.carInfo.clsbdh,
            //     lsh: this.lsh,
            //     jbr: this.jbr
            // };
            // const res = await tickPrint({ content: getCarTick(data) });
            // this.changeStep('down');
            // console.log('打印凭条结果：', res);
        }
    },
    mounted () {
        this.setSteps(VEHICLE_LICENSE_RENEWAL);
    }
};
</script>
