<template>
    <div>
        <div class="kd-page-title">请选择原因</div>
        <div class="kd-page-content-wrapper wrap-info">
            <el-form class="uploadLabel" label-width="11vw">
                <el-form-item label="办理原因：">
                    <el-radio-group v-model="reason">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in reasons"
                            :key="index"
                            :label="item.value"
                            border
                        >
                            {{ item.label }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="goNext" :but-disable="!reason"/>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import audioObj from '@/assets/media/household/11.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'reason-choose',
    props: {
        reasons: { type: Array, default: () => [] },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    data () {
        return {
            reason: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        goUp () {
            this.changeStep('up');
        },
        goNext () {
            this.nextFunction(this.reason);
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        padding-top: 5vw;

        .uploadLabel .uploadChoose {
            width: auto !important;
        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }
    }
</style>
